'use client'

import { useState, useEffect, useRef, useCallback } from 'react'
import type { Player, PlayerResponse, PlayerSuggestion, PlayerSearchResponse } from '@/types/player'
import { logQuery } from '@/utils/queryLogger'
import AskAiButton from '@/components/player-ai/AskAiButton'
import PlayerAiModal from '@/components/player-ai/PlayerAiModal'
import AICautionBanner from '@/components/AICautionBanner'

export default function PlayerStatsPage() {
  const [searchQuery, setSearchQuery] = useState('')
  const [players, setPlayers] = useState<Player[]>([])
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [hasSearched, setHasSearched] = useState(false)

  // Autocomplete state
  const [suggestions, setSuggestions] = useState<PlayerSuggestion[]>([])
  const [showSuggestions, setShowSuggestions] = useState(false)
  const [loadingSuggestions, setLoadingSuggestions] = useState(false)
  const [selectedIndex, setSelectedIndex] = useState(-1)
  const inputRef = useRef<HTMLInputElement>(null)
  const dropdownRef = useRef<HTMLDivElement>(null)
  const debounceTimerRef = useRef<NodeJS.Timeout | null>(null)

  // Ask AI modal state
  const [aiModalOpen, setAiModalOpen] = useState(false)
  const [selectedPlayer, setSelectedPlayer] = useState<Player | null>(null)
  const [seasonGames, setSeasonGames] = useState<any[]>([])

  // Fetch player suggestions for autocomplete
  const fetchSuggestions = useCallback(async (query: string) => {
    if (!query.trim() || query.trim().length < 2) {
      setSuggestions([])
      setShowSuggestions(false)
      return
    }

    setLoadingSuggestions(true)
    try {
      const response = await fetch(`/api/player-search?query=${encodeURIComponent(query)}&limit=8`)
      const data: PlayerSearchResponse = await response.json()

      if (response.ok && data.success) {
        setSuggestions(data.data)
        setShowSuggestions(data.data.length > 0)
      } else {
        setSuggestions([])
        setShowSuggestions(false)
      }
    } catch (err) {
      console.error('Error fetching suggestions:', err)
      setSuggestions([])
      setShowSuggestions(false)
    } finally {
      setLoadingSuggestions(false)
    }
  }, [])

  // Debounced input handler
  const handleInputChange = (value: string) => {
    setSearchQuery(value)
    setSelectedIndex(-1)

    // Clear previous timer
    if (debounceTimerRef.current) {
      clearTimeout(debounceTimerRef.current)
    }

    // Set new timer
    debounceTimerRef.current = setTimeout(() => {
      fetchSuggestions(value)
    }, 300) // 300ms debounce
  }

  // Handle selecting a suggestion
  const selectSuggestion = (suggestion: PlayerSuggestion) => {
    setSearchQuery(suggestion.full_name)
    setShowSuggestions(false)
    setSuggestions([])
    setSelectedIndex(-1)
    // Trigger search immediately
    performSearch(suggestion.full_name)
  }

  // Perform the actual search
  const performSearch = async (query?: string) => {
    const searchTerm = query || searchQuery
    
    if (!searchTerm.trim()) {
      setError('Please enter a player name')
      return
    }

    setLoading(true)
    setError(null)
    setHasSearched(true)
    setShowSuggestions(false)

    try {
      const response = await fetch(`/api/player?name=${encodeURIComponent(searchTerm)}`)
      const data: PlayerResponse = await response.json()

      if (!response.ok || !data.success) {
        throw new Error((data as any).error || 'Failed to fetch player data')
      }

      setPlayers(data.data)
      setError(null)
      
      // Log each player query
      data.data.forEach((player) => {
        logQuery({
          query_type: 'Player Stats',
          details: {
            timestamp: new Date().toISOString(),
            playerName: player.full_name,
            playerId: player.id,
            team: player.team.name,
            position: player.position,
            isActive: player.is_active,
            headlineStats: player.headline_stats
          }
        })
      })
    } catch (err: any) {
      setError(err.message)
      setPlayers([])
    } finally {
      setLoading(false)
    }
  }

  const handleSearch = async (e: React.FormEvent) => {
    e.preventDefault()
    performSearch()
  }

  // Keyboard navigation
  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (!showSuggestions || suggestions.length === 0) return

    switch (e.key) {
      case 'ArrowDown':
        e.preventDefault()
        setSelectedIndex(prev => 
          prev < suggestions.length - 1 ? prev + 1 : prev
        )
        break
      case 'ArrowUp':
        e.preventDefault()
        setSelectedIndex(prev => prev > 0 ? prev - 1 : -1)
        break
      case 'Enter':
        e.preventDefault()
        if (selectedIndex >= 0 && selectedIndex < suggestions.length) {
          selectSuggestion(suggestions[selectedIndex])
        } else {
          performSearch()
        }
        break
      case 'Escape':
        setShowSuggestions(false)
        setSelectedIndex(-1)
        break
    }
  }

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (
        dropdownRef.current &&
        !dropdownRef.current.contains(event.target as Node) &&
        inputRef.current &&
        !inputRef.current.contains(event.target as Node)
      ) {
        setShowSuggestions(false)
        setSelectedIndex(-1)
      }
    }

    document.addEventListener('mousedown', handleClickOutside)
    return () => document.removeEventListener('mousedown', handleClickOutside)
  }, [])

  // Cleanup debounce timer
  useEffect(() => {
    return () => {
      if (debounceTimerRef.current) {
        clearTimeout(debounceTimerRef.current)
      }
    }
  }, [])

  const formatPercentage = (value: number | null | undefined) => {
    if (value === null || value === undefined) return 'N/A'
    return `${(value * 100).toFixed(1)}%`
  }

  const formatNumber = (value: number | null | undefined) => {
    if (value === null || value === undefined) return 'N/A'
    return value.toFixed(1)
  }

  const handleAskAI = async (player: Player) => {
    setSelectedPlayer(player)
    
    // Fetch season game logs if not already loaded
    try {
      const response = await fetch(`/api/player-season-stats?player_id=${player.id}&season=2024-25`)
      const data = await response.json()
      
      if (data.success && data.data?.games) {
        setSeasonGames(data.data.games)
      } else {
        setSeasonGames([])
      }
    } catch (error) {
      console.error('Error fetching season games:', error)
      setSeasonGames([])
    }
    
    setAiModalOpen(true)
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-foreground">Player Statistics</h1>
          <p className="text-muted-foreground mt-1">Search for NBA players and view their information</p>
        </div>
      </div>

      {/* AI Caution Banner */}
      <AICautionBanner />

      {/* Search Form with Autocomplete */}
      <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md p-6 border border-border/50">
        <form onSubmit={handleSearch} className="flex gap-4">
          <div className="flex-1 relative">
            <input
              ref={inputRef}
              type="text"
              placeholder="Start typing player name (e.g., LeBron, Curry, Durant...)"
              value={searchQuery}
              onChange={(e) => handleInputChange(e.target.value)}
              onKeyDown={handleKeyDown}
              onFocus={() => {
                if (suggestions.length > 0) {
                  setShowSuggestions(true)
                }
              }}
              className="w-full px-4 py-3 bg-secondary/50 border border-border text-foreground rounded-lg focus:outline-none focus:ring-2 focus:ring-primary placeholder:text-muted-foreground"
              autoComplete="off"
            />
            
            {/* Autocomplete Dropdown */}
            {showSuggestions && (suggestions.length > 0 || loadingSuggestions) && (
              <div
                ref={dropdownRef}
                className="absolute z-50 w-full mt-2 bg-card border border-border rounded-lg shadow-lg max-h-96 overflow-y-auto custom-scrollbar"
              >
                {loadingSuggestions ? (
                  <div className="p-4 text-center text-muted-foreground">
                    <div className="inline-block animate-spin rounded-full h-5 w-5 border-b-2 border-primary mr-2"></div>
                    Loading suggestions...
                  </div>
                ) : (
                  <ul className="py-2">
                    {suggestions.map((suggestion, index) => (
                      <li key={suggestion.id}>
                        <button
                          type="button"
                          onClick={() => selectSuggestion(suggestion)}
                          className={`w-full px-4 py-3 flex items-center gap-3 hover:bg-secondary/50 transition-colors ${
                            index === selectedIndex ? 'bg-secondary/50' : ''
                          }`}
                        >
                          {/* Player Headshot */}
                          <div className="w-12 h-12 flex-shrink-0">
                            <img
                              src={suggestion.headshot_url}
                              alt={suggestion.full_name}
                              className="w-full h-full rounded-full object-cover bg-gray-100"
                              onError={(e) => {
                                // Fallback to initials if image fails to load
                                const target = e.target as HTMLImageElement
                                target.style.display = 'none'
                                const parent = target.parentElement
                                if (parent) {
                                  parent.innerHTML = `
                                    <div class="w-full h-full bg-gradient-to-br from-blue-600 to-blue-800 rounded-full flex items-center justify-center text-white font-bold text-sm">
                                      ${suggestion.first_name.charAt(0)}${suggestion.last_name.charAt(0)}
                                    </div>
                                  `
                                }
                              }}
                            />
                          </div>
                          
                          {/* Player Info */}
                          <div className="flex-1 text-left">
                            <div className="font-semibold text-foreground">
                              {suggestion.full_name}
                            </div>
                            <div className="text-xs text-muted-foreground">
                              {suggestion.is_active ? (
                                <span className="text-accent font-medium">● Active</span>
                              ) : (
                                <span className="text-muted-foreground">Inactive</span>
                              )}
                            </div>
                          </div>
                          
                          {/* Arrow indicator */}
                          <svg
                            className="w-5 h-5 text-muted-foreground"
                            fill="none"
                            stroke="currentColor"
                            viewBox="0 0 24 24"
                          >
                            <path
                              strokeLinecap="round"
                              strokeLinejoin="round"
                              strokeWidth={2}
                              d="M9 5l7 7-7 7"
                            />
                          </svg>
                        </button>
                      </li>
                    ))}
                  </ul>
                )}
              </div>
            )}
          </div>
          
          <button
            type="submit"
            disabled={loading}
            className="px-8 py-3 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed font-medium hover:scale-105 active:scale-95"
          >
            {loading ? 'Searching...' : 'Search'}
          </button>
        </form>
      </div>

      {/* Error Message */}
      {error && (
        <div className="bg-destructive/10 border border-destructive/20 rounded-lg p-4 animate-slide-up">
          <div className="flex items-center gap-2">
            <span className="text-destructive text-xl">⚠️</span>
            <p className="text-destructive font-medium">{error}</p>
          </div>
        </div>
      )}

      {/* Loading State */}
      {loading && (
        <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md p-12 border border-border/50">
          <div className="flex flex-col items-center justify-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mb-4"></div>
            <p className="text-muted-foreground">Searching for players...</p>
          </div>
        </div>
      )}

      {/* Player Results */}
      {!loading && hasSearched && players.length > 0 && (
        <div className="space-y-6">
          <div className="flex items-center justify-between">
            <h2 className="text-xl font-semibold text-foreground">
              Found {players.length} player{players.length !== 1 ? 's' : ''}
            </h2>
          </div>

          {/* Player Cards Grid */}
          <div className="grid grid-cols-1 gap-6">
            {players.map((player) => (
              <div
                key={player.id}
                className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md p-6 hover:shadow-lg hover:shadow-primary/5 transition-all duration-200 border border-border/50 hover:border-primary/50"
              >
                {/* Player Header */}
                <div className="flex items-start justify-between mb-6 pb-4 border-b border-border/50">
                  <div className="flex items-center gap-4 flex-1">
                    {/* Player Headshot */}
                    <div className="w-24 h-24 flex-shrink-0 relative">
                      <img
                        src={`https://cdn.nba.com/headshots/nba/latest/260x190/${player.id}.png`}
                        alt={player.full_name}
                        className="w-full h-full rounded-lg object-cover border-2 border-border shadow-sm"
                        onError={(e) => {
                          // Fallback to initials badge if headshot fails to load
                          const target = e.target as HTMLImageElement
                          target.style.display = 'none'
                          const parent = target.parentElement
                          if (parent && parent.querySelector('.fallback-badge') === null) {
                            const fallback = document.createElement('div')
                            fallback.className = 'fallback-badge w-full h-full bg-gradient-to-br from-primary to-primary/80 rounded-lg flex items-center justify-center text-primary-foreground font-bold text-3xl border-2 border-border shadow-sm'
                            fallback.textContent = `${player.first_name.charAt(0)}${player.last_name.charAt(0)}`
                            parent.appendChild(fallback)
                          }
                        }}
                      />
                    </div>
                    
                    <div>
                      <h3 className="text-2xl font-bold text-foreground">
                        {player.full_name}
                      </h3>
                      <div className="flex items-center gap-2 mt-1">
                        <span className="inline-block px-3 py-1 text-sm font-semibold rounded-full bg-primary/10 text-primary border border-primary/20">
                          {player.position || 'N/A'}
                        </span>
                        <span className="inline-block px-3 py-1 text-sm font-semibold rounded-full bg-secondary text-foreground border border-border">
                          #{player.jersey}
                        </span>
                        <span className={`inline-block px-3 py-1 text-sm font-semibold rounded-full ${
                          player.is_active ? 'bg-accent/10 text-accent border border-accent/20' : 'bg-secondary text-muted-foreground border border-border'
                        }`}>
                          {player.is_active ? 'Active' : 'Inactive'}
                        </span>
                      </div>
                    </div>
                  </div>
                  
                  {/* Ask AI Button */}
                  <div className="flex-shrink-0">
                    <AskAiButton onClick={() => handleAskAI(player)} />
                  </div>
                </div>

                {/* Player Info Grid */}
                <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
                  {/* Basic Info */}
                  <div className="bg-secondary/30 rounded-lg p-4 border border-border/50">
                    <h4 className="text-sm font-semibold text-muted-foreground mb-3 uppercase">Player Info</h4>
                    <div className="space-y-2">
                      <div className="flex justify-between">
                        <span className="text-sm text-muted-foreground">Team:</span>
                        <span className="text-sm font-medium text-foreground">{player.team.name}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-sm text-muted-foreground">Height:</span>
                        <span className="text-sm font-medium text-foreground">{player.height}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-sm text-muted-foreground">Weight:</span>
                        <span className="text-sm font-medium text-foreground">{player.weight} lbs</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-sm text-muted-foreground">Country:</span>
                        <span className="text-sm font-medium text-foreground">{player.country}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-sm text-muted-foreground">School:</span>
                        <span className="text-sm font-medium text-foreground">{player.school || 'N/A'}</span>
                      </div>
                    </div>
                  </div>

                  {/* Headline Stats */}
                  <div className="bg-primary/10 rounded-lg p-4 border border-primary/20">
                    <h4 className="text-sm font-semibold text-muted-foreground mb-3 uppercase">Current Season Avg</h4>
                    <div className="space-y-2">
                      <div className="flex justify-between">
                        <span className="text-sm text-muted-foreground">Points:</span>
                        <span className="text-lg font-bold text-primary">{formatNumber(player.headline_stats.pts)}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-sm text-muted-foreground">Assists:</span>
                        <span className="text-lg font-bold text-primary">{formatNumber(player.headline_stats.ast)}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-sm text-muted-foreground">Rebounds:</span>
                        <span className="text-lg font-bold text-primary">{formatNumber(player.headline_stats.reb)}</span>
                      </div>
                    </div>
                  </div>

                  {/* Draft Info */}
                  <div className="bg-secondary/30 rounded-lg p-4 border border-border/50">
                    <h4 className="text-sm font-semibold text-muted-foreground mb-3 uppercase">Draft Info</h4>
                    <div className="space-y-2">
                      <div className="flex justify-between">
                        <span className="text-sm text-muted-foreground">Year:</span>
                        <span className="text-sm font-medium text-foreground">{player.draft_year}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-sm text-muted-foreground">Round:</span>
                        <span className="text-sm font-medium text-foreground">{player.draft_round}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-sm text-muted-foreground">Pick:</span>
                        <span className="text-sm font-medium text-foreground">{player.draft_number}</span>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Career Stats */}
                <div className="bg-gradient-to-r from-primary to-primary/80 rounded-lg p-6 mb-6 border border-primary/20">
                  <h4 className="text-lg font-bold text-white mb-4">Career Statistics (Per Game)</h4>
                  <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                    <div className="bg-white/10 rounded-lg p-3">
                      <div className="text-white/80 text-xs uppercase mb-1">Games</div>
                      <div className="text-white text-2xl font-bold">{player.career_stats.games_played}</div>
                    </div>
                    <div className="bg-white/10 rounded-lg p-3">
                      <div className="text-white/80 text-xs uppercase mb-1">PPG</div>
                      <div className="text-white text-2xl font-bold">{formatNumber(player.career_stats.points)}</div>
                    </div>
                    <div className="bg-white/10 rounded-lg p-3">
                      <div className="text-white/80 text-xs uppercase mb-1">APG</div>
                      <div className="text-white text-2xl font-bold">{formatNumber(player.career_stats.assists)}</div>
                    </div>
                    <div className="bg-white/10 rounded-lg p-3">
                      <div className="text-white/80 text-xs uppercase mb-1">RPG</div>
                      <div className="text-white text-2xl font-bold">{formatNumber(player.career_stats.rebounds)}</div>
                    </div>
                    <div className="bg-white/10 rounded-lg p-3">
                      <div className="text-white/80 text-xs uppercase mb-1">FG%</div>
                      <div className="text-white text-xl font-bold">{formatPercentage(player.career_stats.field_goal_pct)}</div>
                    </div>
                    <div className="bg-white/10 rounded-lg p-3">
                      <div className="text-white/80 text-xs uppercase mb-1">3P%</div>
                      <div className="text-white text-xl font-bold">{formatPercentage(player.career_stats.three_point_pct)}</div>
                    </div>
                    <div className="bg-white/10 rounded-lg p-3">
                      <div className="text-white/80 text-xs uppercase mb-1">FT%</div>
                      <div className="text-white text-xl font-bold">{formatPercentage(player.career_stats.free_throw_pct)}</div>
                    </div>
                    <div className="bg-white/10 rounded-lg p-3">
                      <div className="text-white/80 text-xs uppercase mb-1">SPG</div>
                      <div className="text-white text-2xl font-bold">{formatNumber(player.career_stats.steals)}</div>
                    </div>
                  </div>
                </div>

                {/* Recent Season */}
                {player.recent_season.season !== 'N/A' && (
                  <div className="bg-secondary/30 rounded-lg p-6 border border-border/50">
                    <h4 className="text-lg font-semibold text-foreground mb-4">
                      {player.recent_season.season} Season ({player.recent_season.team})
                    </h4>
                    <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                      <div>
                        <div className="text-muted-foreground text-xs uppercase mb-1">Games</div>
                        <div className="text-foreground text-xl font-bold">{player.recent_season.games_played}</div>
                      </div>
                      <div>
                        <div className="text-muted-foreground text-xs uppercase mb-1">PPG</div>
                        <div className="text-foreground text-xl font-bold">{formatNumber(player.recent_season.points)}</div>
                      </div>
                      <div>
                        <div className="text-muted-foreground text-xs uppercase mb-1">APG</div>
                        <div className="text-foreground text-xl font-bold">{formatNumber(player.recent_season.assists)}</div>
                      </div>
                      <div>
                        <div className="text-muted-foreground text-xs uppercase mb-1">RPG</div>
                        <div className="text-foreground text-xl font-bold">{formatNumber(player.recent_season.rebounds)}</div>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Empty State */}
      {!loading && hasSearched && players.length === 0 && !error && (
        <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md p-12 border border-border/50">
          <div className="flex flex-col items-center justify-center text-center">
            <span className="text-6xl mb-4">🔍</span>
            <h3 className="text-xl font-semibold text-foreground mb-2">No Players Found</h3>
            <p className="text-muted-foreground">Try searching for a different player name</p>
          </div>
        </div>
      )}

      {/* Initial State */}
      {!loading && !hasSearched && (
        <div className="bg-gradient-to-r from-primary to-primary/80 rounded-xl shadow-lg p-12 text-primary-foreground border border-primary/20">
          <div className="text-center">
            <span className="text-6xl mb-4 block">🏀</span>
            <h3 className="text-2xl font-bold mb-3">Search for NBA Players</h3>
            <p className="text-primary-foreground/80 mb-6">
              Enter a player's name above to view their profile, team information, and physical stats.
            </p>
            <div className="flex flex-wrap justify-center gap-2">
              <span className="px-3 py-1 bg-primary-foreground/10 rounded-full text-sm border border-primary-foreground/20">Try: LeBron James</span>
              <span className="px-3 py-1 bg-primary-foreground/10 rounded-full text-sm border border-primary-foreground/20">Try: Stephen Curry</span>
              <span className="px-3 py-1 bg-primary-foreground/10 rounded-full text-sm border border-primary-foreground/20">Try: Giannis</span>
            </div>
          </div>
        </div>
      )}

      {/* Ask AI Modal */}
      {selectedPlayer && (
        <PlayerAiModal
          isOpen={aiModalOpen}
          onClose={() => setAiModalOpen(false)}
          player={selectedPlayer}
          seasonGames={seasonGames}
        />
      )}
    </div>
  )
}
